import React, {Fragment} from 'react';

import AboutPage from 'abstractions/about-page/about-page';
import TopbarNoauthen from 'components/topbar-noauthen/topbar-noauthen';
import Topbar from 'components/topbar/topbar';
import Footer from 'components/footer/footer';

import './intro.scss';

export default class Intro extends AboutPage {
  render() {
    const {search, isAuthenticated} = this.state;
    return (
      <Fragment>
        {
          isAuthenticated !== null
            ? isAuthenticated 
              ? <Topbar search={search} onSearchChange={this.handleSearchChange} />
              : <TopbarNoauthen />
            : null
        }
        <div className="grid-x grid-padding-x app-intro">
          <div className="cell large-6 large-offset-3 medium-10 medium-offset-1 small-12">
            <h3 className="text-center text-uppercase margin-bottom-2">Gii thiu</h3>
            <p className="text-justify">
              Mobile Computer Testing System l b cng c nh gi my tnh c ng phc v vic phn tch, nh gi t c bn n nng cao tnh trng ATTT ca cc my tnh chy trn cc nn tng khc nhau (x86, x64, ...), h tr cn b k thut thu thp, tm kim v phn tch d liu t ng t cc my tnh, ng thi trch xut bo co theo ngn ng ting Vit vi giao din trc quan.
            </p>
          </div>
        </div>
        <Footer />
      </Fragment>
    );
  }
}
